#Clouds {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #326696;
  background: linear-gradient(356deg, #000763 0%, #326696 100%);
  opacity: 0;
  transition-property: opacity;
  transition-duration: 2.5s;
  transition-delay: 0.5s;
}
#Clouds.Visible {
  opacity: 1;
}